<template>
  <div class="card">
    <div class="card-header border-0">
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0">Tables</h3>
        </div>
        <div class="col text-right">
          <base-button size="sm" type="primary">See all</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table thead-classes="thead-light" :data="tableData">
        <template slot="columns">
          <th>Name</th>
          <th>Fields</th>
          <th>Action</th>
        </template>

        <template slot-scope="{row}">
          <th scope="row">{{row.name}}</th>
          <td>{{row.fields}}</td>
          <td>
            <base-button size="sm" type="primary" iconOnly icon="ni ni-ruler-pencil" title="Edit"></base-button>
          </td>
        </template>
      </base-table>
    </div>
  </div>
</template>
<script>
export default {
  name: "data-tables-table",
  data() {
    return {
      tableData: []
    };
  },
  mounted() {
    var dt = this;
    this.$http.get("/api/data-table").then(response => {
      for (var d in response.data) {
        dt.tableData.push({
          id: response.data[d].id,
          name: response.data[d].name,
          fields: response.data[d].name
        });
      }
    });
  }
};
</script>
